//该文件是整个项目的入口文件

//引入Vue
import Vue from 'vue'
//引入App组件，他是所有组件的父组件
import App from './App.vue'

//引入路由插件
import VueRouter from 'vue-router'
// //引入路由器
import router from './router'
//关闭vue的生产提示
Vue.config.productionTip = false
//使用插件
Vue.use(VueRouter)//应用路由插件
//创建vue实例对象   vm
new Vue({
  el:'#app',
  // 完成将App组件放进容器中 ↓
  render : h => h(App),
  router:router
})//.$mount('#app')
