<template>
    <h2>我是About的内容</h2>
</template>

<script>
    export default {
        name: "About",
        //通过路由规则，进入该组件时被调用 进入时守卫
        beforeRouteEnter(to,from,next){
            if (to.meta.isAuth){
             if (localStorage.getItem('school')==='atguigu'){
              next()
               }else {
                        alert('不是atguigu无权查看')
                    }
                    }else {
                    next()
                    }
        },
        //通过路由规则，离开该组件时被调用 离开时守卫
        beforeRouteUpdate(to,from,next){
            next()
        }
    }
</script>

