//该文件专门用于创建整个应用的路由器
import VueRouter from "vue-router";
//引入组件
import About from "../pages/About";
import Home from "../pages/Home";
import News from "../pages/News";
import Message from "../pages/Message";
import Detail from "../pages/Detail";
//创建并且暴露一个路由
const router=new VueRouter({
    mode:'history',
    routes:[
        //一级路由
        {
            name:'guanyu',
            path:'/about',
            component:About,
            meta:{
                isAuth:true,
                title:'关于'
            }

        },
        {
            name: 'zhuye',
            path:'/home',
            component:Home,
            meta:{
                title:'主页'
            },
            //二级路由 path不加/

            children:[
                {
                    name: 'xinwen',
                    path:'news',
                    component:News,
                    //路由元信息
                    //控制是否需要权限 true是需要权限 false是不需要权限
                    meta:{title:'新闻', isAuth:true},
                    // beforeEnter:(to,from,next)=>{
                    //     //独享路由守卫没有独享的后置路由守卫
                    //     if (to.meta.isAuth){
                    //     if (localStorage.getItem('school')==='atguigu'){
                    //     next()
                    //     }else {
                    //         alert('不是atguigu无权查看')
                    //     }
                    //     }else {
                    //     next()
                    //     }
                    // },
                },
                {
                    name: 'xiaoxi',
                    path: 'message',
                    component: Message,
                    meta:{
                        title: '消息',
                        isAuth:true
                    },
                    children:[
                        {
                            name:'xiangqing',
                            //这样占位才可以用params参数
                            //path:'detail/:id/:title',
                            path:'detail',
                            component:Detail,
                            meta:{
                                title:'详情'
                            },
                            //props的第一种写法，值为对象，该对象中所有的key-value都会以props的形式传给Detail组件
                           // props:{a:1,b:'hello'}

                            //props的第二种写法，值为布尔值，值为真就会把路由组件接收的所有params参数，以props的形式传给Detail组件
                            //props:true

                            //props的第三种写法，值为函数
                            props($route){
                                return{id:$route.query.id,title:$route.query.title}
                            }
                        }
                    ]
                },

            ]
        },


    ]
})
