//         //前往的地址限制
//     //if (to.path === '/home/news'|| to.path === '/home/message'){
//
//     if (to.meta.isAuth){//判断是否需要鉴定权限
//     if (localStorage.getItem('school')==='atguigu'){
//             next()
//         }else {
//             alert('不是atguigu无权查看')
//         }
//         }else {
//             next()
//     }
// })
// //全局后置路由守卫  初始化的时候被调用、每次切换路由后被调用
